'use strict'; // MODULES //

var evalpoly = require('./evalpoly.js'); // MAIN //

/**
* Generates a function for evaluating a polynomial.
*
* ## Notes
*
* -   The compiled function uses [Horner's rule][horners-method] for efficient computation.
*
* [horners-method]: http://en.wikipedia.org/wiki/Horner%27s_method
*
*
* @param {NumericArray} c - polynomial coefficients sorted in ascending degree
* @returns {Function} function for evaluating a polynomial
*
* @example
* var polyval = factory( [3.0,2.0,1.0] );
*
* var v = polyval( 10.0 ); // => 3*10^0 + 2*10^1 + 1*10^2
* // returns 123.0
*
* v = polyval( 5.0 ); // => 3*5^0 + 2*5^1 + 1*5^2
* // returns 38.0
*/


function factory(c) {
  var f;
  var n;
  var m;
  var i; // Avoid exceeding the maximum stack size on V8 :(. Note that the choice of `500` was empirically determined...

  if (c.length > 500) {
    return polyval;
  } // Code generation. Start with the function definition...


  f = 'return function evalpoly(x){'; // Create the function body...

  n = c.length; // If no coefficients, the function always returns 0...

  if (n === 0) {
    f += 'return 0.0;';
  } // If only one coefficient, the function always returns that coefficient...
  else if (n === 1) {
      f += 'return ' + c[0] + ';';
    } // If more than one coefficient, apply Horner's method...
    else {
        // If `x == 0`, return the first coefficient...
        f += 'if(x===0.0){return ' + c[0] + ';}'; // Otherwise, evaluate the polynomial...

        f += 'return ' + c[0];
        m = n - 1;

        for (i = 1; i < n; i++) {
          f += '+x*';

          if (i < m) {
            f += '(';
          }

          f += c[i];
        } // Close all the parentheses...


        for (i = 0; i < m - 1; i++) {
          f += ')';
        }

        f += ';';
      } // Close the function:


  f += '}'; // Add a source directive for debugging:

  f += '//# sourceURL=evalpoly.factory.js'; // Create the function in the global scope:

  return new Function(f)(); // eslint-disable-line no-new-func

  /*
  *    function evalpoly( x ) {
  *        if ( x === 0.0 ) {
  *            return c[ 0 ];
  *        }
  *        return c[0]+x*(c[1]+x*(c[2]+x*(c[3]+...+x*(c[n-2]+x*c[n-1]))));
  *    }
  */

  /**
  * Evaluates a polynomial.
  *
  * @private
  * @param {number} x - value at which to evaluate a polynomial
  * @returns {number} evaluated polynomial
  */

  function polyval(x) {
    return evalpoly(c, x);
  }
} // EXPORTS //


module.exports = factory;