'use strict'; // MODULES //

var isnan = require('./../../../../base/assert/is-nan');

var isInteger = require('./../../../../base/assert/is-integer');

var gamma = require('./../../../../base/special/gamma');

var PINF = require('@stdlib/constants/float64/pinf');

var FACTORIALS = require('./factorials.json'); // VARIABLES //


var MAX_FACTORIAL = 170; // TODO: consider extracting as a constant
// MAIN //

/**
* Evaluates the factorial of `x`.
*
* @param {number} x - input value
* @returns {number} factorial
*
* @example
* var v = factorial( 3.0 );
* // returns 6.0
*
* @example
* var v = factorial( -1.5 );
* // returns ~-3.545
*
* @example
* var v = factorial( -0.5 );
* // returns ~1.772
*
* @example
* var v = factorial( 0.5 );
* // returns ~0.886
*
* @example
* var v = factorial( -10.0 );
* // returns NaN
*
* @example
* var v = factorial( 171.0 );
* // returns Infinity
*
* @example
* var v = factorial( NaN );
* // returns NaN
*/

function factorial(x) {
  if (isnan(x)) {
    return NaN;
  }

  if (isInteger(x)) {
    if (x < 0) {
      return NaN;
    }

    if (x <= MAX_FACTORIAL) {
      return FACTORIALS[x];
    }

    return PINF;
  }

  return gamma(x + 1.0);
} // EXPORTS //


module.exports = factorial;