'use strict';
/*
* Notes:
*	=> sin(-x) = -sin(x)
*	=> sin(+n) = +0, where `n` is a positive integer
*	=> sin(-n) = -sin(+n) = -0, where `n` is a positive integer
*	=> cos(-x) = cos(x)
*/
// MODULES //

var isnan = require('./../../../../base/assert/is-nan');

var isInfinite = require('./../../../../base/assert/is-infinite');

var cos = require('./../../../../base/special/cos');

var sin = require('./../../../../base/special/sin');

var abs = require('./../../../../base/special/abs');

var copysign = require('./../../../../base/special/copysign');

var PI = require('@stdlib/constants/float64/pi'); // MAIN //

/**
* Computes the value of `sin(πx)`.
*
* @param {number} x - input value
* @returns {number} function value
*
* @example
* var y = sinpi( 0.0 );
* // returns 0.0
*
* @example
* var y = sinpi( 0.5 );
* // returns 1.0
*
* @example
* var y = sinpi( 0.9 );
* // returns ~0.309
*
* @example
* var y = sinpi( NaN );
* // returns NaN
*/


function sinpi(x) {
  var ar;
  var r;

  if (isnan(x)) {
    return NaN;
  }

  if (isInfinite(x)) {
    return NaN;
  } // Argument reduction (reduce to [0,2))...


  r = x % 2.0; // sign preserving

  ar = abs(r); // If `x` is an integer, the mod is an integer...

  if (ar === 0.0 || ar === 1.0) {
    return copysign(0.0, r);
  }

  if (ar < 0.25) {
    return sin(PI * r);
  } // In each of the following, we further reduce to [-π/4,π/4)...


  if (ar < 0.75) {
    ar = 0.5 - ar;
    return copysign(cos(PI * ar), r);
  }

  if (ar < 1.25) {
    r = copysign(1.0, r) - r;
    return sin(PI * r);
  }

  if (ar < 1.75) {
    ar -= 1.5;
    return -copysign(cos(PI * ar), r);
  }

  r -= copysign(2.0, r);
  return sin(PI * r);
} // EXPORTS //


module.exports = sinpi;